"use client"
import LogoName from '@/components/Navbar/LogoName';
import React, { useState } from 'react';

const Register = () => {
  const [email, setEmail] = useState('');


  const handleSubmit = (event: { preventDefault: () => void; }) => {
    event.preventDefault();
    // اینجا کد مربوط به ارسال اطلاعات به سرور یا پردازش اطلاعات فرم قرار می‌گیرد
    console.log('ایمیل:', email);
  };

  return (
    <div className='flex flex-col top-14 '>

      <div className='flex justify-center p-5'>
        <LogoName />
      </div>

      <div className='flex justify-center'>
        <h2 >ورود</h2>
        <h2 >|</h2>
        <h2 >ثبت نام</h2>
      </div>

      <form onSubmit={handleSubmit}>
        <div className='px-10'>
          <input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            placeholder='ایمیل خود را وارد کنید'
          />

        </div>

        <div className='flex justify-center'>
          <button className='p-3 bg-slate-400' >ادامه</button>
        </div>

      </form>
    </div>
  );
};

export default Register;
